import React from 'react';
import { Jumbotron, Button, NavItem, } from 'reactstrap';
import './about.css';
import 'bootstrap/dist/css/bootstrap.min.css';


const Example = () => {
    return (
        <div>
            <Jumbotron>
                <div className="jumbo jumbotron-fluid">
                    <div className="container">
                        <nav className="navabar ">
                            <div className="container">
                                <h1 className="navabar-brand ">Cek Cuaca</h1>
                                <Button outline color="warning" size="sm" className="home" active href="https://deyanazhari.github.io/Cekcuaca">Home</Button>
                                <Button outline color="warning" size="sm" className="about" active href="https://deyanazhari.github.io/cekcuaca-about/">About</Button>
                                <Button outline color="warning" size="sm" className="cek" active href="https://deyanazhari.github.io/cekcuaca-search/">Cek Cuaca</Button>

                                <NavItem className="navabar-img">
                                    <img width="50px" src={'./logo.png'} />
                                </NavItem>
                                <h2 className="text">
                                    Apa Itu Web Cuaca?
                                </h2>
                                <p className="span" >Web cuaca adalah sebuah web yang memberi sebuah<br /> informasi tentang kondisi cuaca saat ini.
                                    Website ini juga dapat memberikan <br /> informasi cuaca hingga 200.000 kota yang ada di Indonesia secara REALTIME 24 JAM!! </p>
                            </div>
                        </nav>
                    </div>
                </div >
            </Jumbotron >
        </div >
    );
};

export default Example;